import React, { useState, useEffect, useRef } from "react";
import { Form } from "react-bootstrap";
import { makeStyles } from "@material-ui/core";
import styles2 from "../css/COLUMN.module.css";
import ElemDeleteControl from "./ElemDeleteControlWrapper";
import styles from "../css/Link.module.css";
import EditableLabel from "../../ReusableComponents/EditableLabel/EditableLabel";
import QuickTools from "../../ReusableComponents/QuickTools/QuickTools";
import { getSuggestedId } from "../../components/IFormDesignerLayout/Logic/UserIdLogic";
import { handleDisplayProperty } from "../../components/IFormDesignerLayout/IFormDesignerLogic";
import { GetState, GetStateFromData } from "../../components/CustomHooks/CustomHooks";
import cloneDeep from "lodash-es/cloneDeep";
import { functions } from "../../components/IFormDesignerLayout/ApplicationAPI/applicationApi";

const useStyles = makeStyles({
  linkHover: {
    "&:hover": {
      textDecoration: (style) => (style?.["Enable"] === "true" ? "underline" : "none !important")
    }
  }
});

const LINK = (props) => {
  const data = props.data;
  const labelPropStyle = props?.data?.style || {
    Label: "",
    Visible: "",
    MobileVisible: "",
    TabsVisible: "",
    LabelAlignment: "",
    FontFamily: "",
    FontSize: "",
    FontWeight: "",
    FontColor: "",
    BackColor: "",
    BackgroundColor: ""
  };

  const classes = useStyles(labelPropStyle);

  const contexts = GetStateFromData(props.containerId);
  const {tempCanWidth,controlSelected} = GetState("tempCanWidth","controlSelected");
  const isSubformInPage = props?.isSubformInPage || false;

  const {
    IdPrecedence,
    openQuickToolFor,
    modeReadOrWrite,
    deleteControl,
    formState2,
    device,
    showAlertConfirmNotifications
  } = contexts.contexts;
  const conditionalQuickToolOpening =
    props.id === openQuickToolFor ? true : false

  let Editable = props.data.style.Enable;

  const refFlag8 = useRef(0);

  const refFlag7 = useRef(0);

  if (openQuickToolFor !== undefined && props.id === openQuickToolFor) {
    refFlag8.current = 1;
  }
  if (props.previewmode === undefined || props.previewmode === false) {
    refFlag7.current = 0;
  }

  if (props.previewmode && modeReadOrWrite === "r") {
    props.data.style.Enable = "false";
    Editable = "false";
    refFlag7.current = 1;
  }
  if (refFlag8.current === 1 && props.data.style["Enable"] === "false" && refFlag7.current === 0) {
    Editable = "false";
    props.data.style.Enable = "false";
  }
  if (refFlag8.current === 1 && props.data.style["Enable"] === "true" && refFlag7.current === 0) {
    Editable = "true";
    props.data.style.Enable = "true";
  }

  const themeData = formState2.theme;

  const actualControlId = props.data.id;
  const mappingId = props?.data?.["style"]?.["MappingId"];
  const userDefinedId = props?.data?.["style"]?.["CustomId"];
  const userDefinedControlId = getSuggestedId(actualControlId, mappingId, userDefinedId, IdPrecedence);

  const [open, setOpen] = useState(conditionalQuickToolOpening);
  const [mobileDisplay, setMobileDisplay] = useState(true);
  const [tabDisplay, setTabDisplay] = useState(true);
  const startMobile = useRef();
  const endMobile = useRef();
  const startTab = useRef();
  const endTab = useRef();

  useEffect(() => {
    if (formState2?.style) {
      startMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]);
      endMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]);
      startTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]);
      endTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]);
    }
  }, [
    formState2.style?.["Breakpoint"],
    formState2.style?.["Breakpoint"]?.["Mobile"],
    formState2.style?.["Breakpoint"]?.["Tab"]
  ]);

  useEffect(() => {
    if (props?.previewmode)
      handleDisplayProperty(
        props,
        device,
        setMobileDisplay,
        setTabDisplay,
        startMobile.current,
        endMobile.current,
        startTab.current,
        endTab.current
      );
  }, [
    props?.data?.style?.["MobileVisible"],
    props?.data?.style?.["TabsVisible"],
    device,
    endMobile,
    endTab,
    startMobile,
    startTab,
    tempCanWidth
  ]);
  const translationStyles1=window?.appformBuilder_configs?.selectedLanguage === "ar" || window?.appformBuilder_configs?.selectedLanguage === "ar-SA";

const defaultStyle = {
  font_family: "Open Sans",
  font_size: "14px",
  font_weight: "normal",
  font_color: "#0072C6",
  back_color: "#fff",
  font_style: "normal",
  text_align: translationStyles1 ? "right" : "left",
  padding_left: "0px",
  padding_right: "0px",
  padding_top: "0px",
  padding_bottom: "0px",
  enterUrl: labelPropStyle["EnterURL"],
  methodName: labelPropStyle["MethodName"]
}
const [rerenderState, setReRenderState] = useState(false);
  const linkDefaultStyles = useRef(defaultStyle);

  const setLabelStyles = (newStyle) => {
    const prevState = JSON.stringify(linkDefaultStyles.current);
    linkDefaultStyles.current = { ...linkDefaultStyles.current, ...newStyle };
    if(!props.previewmode) {
      if(prevState !== JSON.stringify(linkDefaultStyles.current)) {
        setReRenderState(!rerenderState);
      }
    }
  };

  useEffect(() => {
    if (themeData?.["BasicTheme"]?.["FontFamily"]) {
      setLabelStyles({
        font_family:
          themeData["BasicTheme"]["FontFamily"] === "Select" ? "Open Sans" : themeData["BasicTheme"]["FontFamily"]
      });
    }
    if (themeData?.["BasicTheme"]?.["FontSize"]) {
      setLabelStyles({ font_size: themeData["BasicTheme"]["FontSize"] });
    }
  }, [themeData?.["BasicTheme"], labelPropStyle?.["renderTheme"]]);

  useEffect(() => {
    if (labelPropStyle?.["FontColor"]) {
      setLabelStyles({ font_color: labelPropStyle["FontColor"] });
    }
    if (labelPropStyle?.["FontFamily"]) {
      setLabelStyles({ font_family: labelPropStyle["FontFamily"] });
    }
    if (labelPropStyle?.["FontSize"]) {
      setLabelStyles({ font_size: labelPropStyle["FontSize"] });
    }
    if (labelPropStyle?.["FontWeight"]) {
      setLabelStyles({ font_weight: labelPropStyle["FontWeight"] });
    } else {
      setLabelStyles({ font_weight: defaultStyle["font_weight"] });
    }
    if (labelPropStyle?.["FontStyle"]) {
      setLabelStyles({ font_style: labelPropStyle["FontStyle"] });
    } else {
      setLabelStyles({ font_style: defaultStyle["font_style"] });
    }
    if (labelPropStyle?.["BackColor"]) {
      setLabelStyles({ back_color: labelPropStyle["BackColor"] });
    }
    if (labelPropStyle?.["TextAlignment"]) {
      setLabelStyles({ text_align: labelPropStyle["TextAlignment"] });
    }
    if (labelPropStyle?.["LinkAlignment"]) {
      setLabelStyles({ text_align: labelPropStyle["LinkAlignment"] });
    }
    if (labelPropStyle?.["SpacingLeft"]) {
      setLabelStyles({ padding_left: labelPropStyle?.["SpacingLeft"] });
    }
    if (labelPropStyle?.["SpacingRight"]) {
      setLabelStyles({ padding_right: labelPropStyle?.["SpacingRight"] });
    }
    if (labelPropStyle?.["SpacingTop"]) {
      setLabelStyles({ padding_top: labelPropStyle?.["SpacingTop"] });
    }
    if (labelPropStyle?.["SpacingBottom"]) {
      setLabelStyles({ padding_bottom: labelPropStyle?.["SpacingBottom"] });
    }
  }, [cloneDeep(labelPropStyle)]);

  const inputStyle = {
    pointerEvents: props.data.style.Enable === "false" ? "none" : "initial",
    color: props.data.style.Enable === "false" ? "#c4c4c4" : linkDefaultStyles.current.font_color,
    fontSize: linkDefaultStyles.current.font_size,
    fontFamily: linkDefaultStyles.current.font_family,
    fontWeight: linkDefaultStyles.current.font_weight,
    fontStyle: linkDefaultStyles.current.font_style,
    cursor: props.data.style.Enable === "false" ? "unset" : "pointer",
    textDecoration: props.data.style.Enable === "false" ? "unset" : "underline",
    "white-space":"break-spaces"
  };
  const inputStyle1 = {
    pointerEvents: "initial",
    color: linkDefaultStyles.current.font_color,
    fontSize: linkDefaultStyles.current.font_size,
    fontFamily: linkDefaultStyles.current.font_family,
    fontWeight: linkDefaultStyles.current.font_weight,
    fontStyle: linkDefaultStyles.current.font_style,
    cursor: "pointer",
    textDecoration: "underline",
    "white-space":"break-spaces"
  };

  const inputStyleGroup = {
    textAlign: linkDefaultStyles.current.text_align,
    backgroundColor: linkDefaultStyles.current.back_color,
    paddingLeft: linkDefaultStyles.current.padding_left,
    paddingRight: linkDefaultStyles.current.padding_right,
    paddingTop: linkDefaultStyles.current.padding_top,
    paddingBottom: linkDefaultStyles.current.padding_bottom,
    display: "block"
  };

  const [defaultLabel, setDefaultLabel] = useState(props?.data?.style?.["Label"]);
  const [editLabel, setEditLabel] = useState(false);
  
  const bringEditableLabel = () => setEditLabel(true);

  useEffect(() => setDefaultLabel(props?.data?.style?.["Label"]), [props?.data?.style?.["Label"]]);

  //Sets the label value into the formState
  const setLabelHandler = (recievedLabel) => {
    try {
      if (!recievedLabel) {
        showAlertConfirmNotifications({
          open: true,
          showConfirm: "showNotification",
          alertType: "error",
          alertContentKey: "label is required",
          autoHideDuration: 6000
        });
      } else if (props?.data?.style?.Label) {
        setDefaultLabel((prevDefaultLabel) => {
          if (prevDefaultLabel === recievedLabel) {
            return prevDefaultLabel;
          } else {
            props.data.style["Label"] = recievedLabel;
            return recievedLabel;
          }
        });
        setEditLabel(false);
      }
    } catch (err) {
      console.error(err);
    }
  };

  function handleClick() {
    if (props?.data?.style?.OnClick === "URL" && props?.data?.style?.LinkURL) {
      window.open(props.data.style.LinkURL, "", "top=100,left=200,width=900,height=900");
    } else if (props?.data?.style?.OnClick === "Code" && props?.data?.style?.MethodName) {
      const customeFunction = props.data.style.MethodName;

      if (window[customeFunction]) {
        window[customeFunction]();
        functions.updateJSON();
      }
    }

    // Custom hook
    if (window.clickLabelLink) {
      window.clickLabelLink(props.data.id);
    }
  }

  const [, setRenderState] = useState(true);
  useEffect(() => {
    setRenderState((prev) => !prev);
  }, []);

  if (props.previewmode) {
    return (
      <>
        {props?.data?.style && props.data.style["Visible"] === "true" ? (
          <Form.Group
            style={mobileDisplay === false || tabDisplay === false ? { display: "none" } : inputStyleGroup}
            className={styles.borderStylePreview + " spacingThemeControls "}
            id={userDefinedControlId + "_group"}
            controlId="dob"
            onDragStart={props.handleReorderStart}
            onDrop={props.handleReorderDrop}
            data-placement="top"
            control_id={userDefinedControlId + "_group"}
            key={data.id}
            elem_type="link"
            frameindex={props.data.frameIndex}
            elementindex={props.data.elementIndex}
            sheetIndex={props.data.sheetIndex}
            sheetElementIndex={props.data.sheetElementIndex}
            sheetFrameElementindex={props.data.sheetFrameElementindex}
          >
            <a
              className={["spacingThemeLabel", "controlFontSize", classes.linkHover].join(" ")}
              onClick={handleClick}
              style={inputStyle}
              elem_type="link"
              id={userDefinedControlId}
              control_id={userDefinedControlId}
              frameindex={props.data.frameIndex}
              elementindex={props.data.elementIndex}
            >
              {props?.data?.value || props?.data?.style?.["Label"]}
            </a>
          </Form.Group>
        ) : null}
      </>
    );
  } else {
    return (
      <Form.Group
        style={inputStyleGroup}
        onClick={isSubformInPage ? {} : (e) => props.handleSelected(e, data.id, "n")}
        
        className={styles2.formStyle + " spacingThemeControls "}
        id={data.id + "_group"}
        controlId="dob"
        draggable={true}
        onDragStart={props.handleReorderStart}
        onDrop={props.handleReorderDrop}
        data-placement="top"
        control_id={data.id}
        key={data.id}
        tabIndex="1"
        elem_type="link"
        frameindex={props.data.frameIndex}
        elementindex={props.data.elementIndex}
        sheetIndex={props.data.sheetIndex}
        sheetElementIndex={props.data.sheetElementIndex}
        sheetFrameElementindex={props.data.sheetFrameElementindex}
        onKeyDown={(e) => props.handleSelected(e)}
      >
        <a
          className={["spacingThemeLabel", "controlFontSize", classes.linkHover].join(" ")}
          elem_type="link"
          id={userDefinedControlId}
          control_id={data.id}
          frameindex={props.data.frameIndex}
          elementindex={props.data.elementIndex}
        >
          <EditableLabel
            defaultLabel={
              <div onDoubleClick={bringEditableLabel} style={inputStyle1}>
                {props?.data?.value || props?.data?.style?.["Label"]}
              </div>
            }
            editlabel={editLabel}
            default_value={defaultLabel}
            setLabelHandler={(recievedLabel) => setLabelHandler(recievedLabel)}
          />
        </a>
        {controlSelected?.id === props?.id &&
        <ElemDeleteControl
          isSubformInPage={isSubformInPage}
          containerId={props.containerId}
          id={props.data.id}
          deleteControl={deleteControl}
          elemType={props.data?.elem_type}
          setDragging={props.setDragging}
          handleReorderStart={props.handleReorderStart}
          handleReorderDrop={props.handleReorderDrop}
          data={props.data}
          dragId={props.dragId}
          dragType={"column"}
        />
  }
        {window?.openQuickTools && open &&  (
          <QuickTools containerId={props.containerId} selectedControl={props.data} open={open} setOpen={setOpen} />
        )}
      </Form.Group>
    );
  }
};

export default LINK;
